import MUIDataTable from "mui-datatables";
import React, { useState, useEffect } from "react";
import Button from "@material-ui/core/Button";
import { columns, data, errorCols, errorOptions } from "./data_header_table";
import { createMuiTheme, MuiThemeProvider } from "@material-ui/core/styles";
import { Formik, Form, ErrorMessage } from 'formik'
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import * as Yup from 'yup'
import Swal from 'sweetalert2'
import { Fab } from "@material-ui/core";
import { Grid, TextField } from '@material-ui/core';
import { Autocomplete } from "@material-ui/lab"
import { requesicoes } from "../../../utils/services";
import Input from '../../../ui-component/shared/Input';
import { MoonLoader } from 'react-spinners'; // Importe o componente BounceLoader


let provincias = [];
let cidades = [];
export default function MuiDatatableExample() {
  const getMuiTheme = () =>
    createMuiTheme({
      overrides: {
        MUIDataTableHeadCell: {
          fixedHeader: { backgroundColor: "darkblue", color: "white" }
        },
        MUIDataTableSelectCell: {
          headerCell: { backgroundColor: "darkblue" },
          checkboxRoot: { color: "white", filter: "brightness(0.5)" },
          expandDisabled: {
            // Soft hide the button.
            visibility: "hidden"
          }
        }
      }
    });

  const [isFormValid, setIsFormValid] = useState(false);
  const [isLoading, setIsLoading] = useState(true);
  const [openDistrito, setOpenDistrito] = React.useState(false);
  const [updateDom, setUpdateDom] = useState(false);
  const [carregando, setCarregando] = useState(true);


  useEffect(() => {
    init();
    setTimeout(() => {
      setCarregando(false);
    }, 2000);
  }, [])

  async function init() {

    const [listaProvincias, listaCidades] = await Promise.all([requesicoes().buscarProvincia(), requesicoes().buscarCidades()]);
    provincias = listaProvincias.data
    cidades = listaCidades.data.map((c, i) => ({ ...c, NO: i + 1, provincia: c.regiaoPai.name, pais: c.regiaoPai.country_id.name }));

    setUpdateDom(!updateDom);
  }


  const handleClickOpenDistrito = () => {
    setOpenDistrito(true);
  };

  const handleClose = () => {
    setOpenDistrito(false);
  };

  const options = {
    // filterType: "checkbox",
    responsive: "standard",
    elevation: 10,
    print: true,
    selectToolbarPlacement: "true",
    selectableRowsOnClick: true,
    expandableRows: false,
    expandableRowsHeader: false,
    isRowExpandable: (dataIndex, expandedRows) => {
      const { errors } = cidades[dataIndex];
      return errors !== null && errors !== undefined;
    },
    onRowExpansionChange: (curExpanded, allExpanded, rowsExpanded) =>
      console.log(curExpanded, allExpanded, rowsExpanded),
    renderExpandableRow: (rowData, rowMeta) => {

      return (
        <div
          style={{
            marginLeft: "90%",
            width: "100%",
            marginTop: "3%",
            marginBottom: "3%"
          }}
        >
          <MUIDataTable
            columns={errorCols}
            cidades={cidades[5]}
            options={errorOptions}
          />
        </div>
      );
    },
    customToolbarSelect: (selectedRows, displayData, setSelectedRows) => {
      console.log(selectedRows, displayData);
      return <div> am youre toolbar header </div>;
    },
    nextPath(path) {
      this.props.history.push(path);
    },
    customToolbar: () => {

      return (
        <div>
          <Fab onClick={handleClickOpenDistrito} variant="extended" size="medium"
            style={{
              position: 'fixed',
              bottom: '60px', zIndex: 500,
              right: '15px'
            }} color="primary">Adicionar</Fab>
        </div>
      );
    },
    onRowSelectionChange: (
      currentRowsSelected,
      allRowsSelected,
      rowsSelected
    ) => {
      if (
        allRowsSelected !== null &&
        allRowsSelected !== undefined &&
        allRowsSelected.length > 0
      ) {
        setIsFormValid(true);
      } else {
        setIsFormValid(false);
      }
      console.log(currentRowsSelected, allRowsSelected, rowsSelected);
      rowsSelected.map((item) => console.log(cidades[item]));
    }
  };
  const validationSchema = Yup.object().shape({
    name: Yup.string().min(3, "Deve ter no minio 3 caratere").required("Campo Obrigatorio"),
  })
  return (
    <div>

      {carregando ? (
        <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', width: '100%', marginTop: '20%' }}>
          <MoonLoader color="#36d7b7" />
        </div>

      ) : (
        <div>
          <MuiThemeProvider theme={getMuiTheme()}>
            <MUIDataTable
              title={'Listagem de Cidades'}
              columns={columns(cidades)}
              data={cidades}
              options={options}
            // components={components}
            />
          </MuiThemeProvider>


          {/* Cadastro de distrito */}

          <Dialog open={openDistrito} onClose={handleClose} aria-labelledby="form-dialog-title">
            <DialogTitle id="form-dialog-title">Cadastrar Distrito</DialogTitle>
            <DialogContent>
              <DialogContentText>
                Preencha os dados da cidade que pretende cadastrar
              </DialogContentText>
              <Formik initialValues={{}} validationSchema={validationSchema}

                onSubmit={(values, { setSubmitting }) => {
                  async function save(values) {

                    try {

                      await Promise.resolve(requesicoes().postCidade([values]));
                      //setOpen(true)
                      Swal.fire(
                        'Exito!',
                        'Cidade adicionada com sucesso!',
                        'success'
                      )
                      init();

                    } catch (error) {
                      Swal.fire({
                        icon: 'error',
                        title: 'Oops...',
                        text: 'Ocorreu um erro ao cadastrar Curso!!',
                        footer: 'Se o erro pressistir porfavor contacte o centro Informatico'
                      })

                    }
                  }
                  save(values);
                  setTimeout(() => {
                    setSubmitting(false);
                  }, 500);
                }}>
                {(props) => {
                  const { isSubmitting, setFieldValue, values } = props;
                  return (
                    <Form>
                      <Grid container spacing={2}>
                        <Grid item xs={12} sm={6}>
                          <Input name="name"
                            label="Nome"
                            placeholder="Nome da cidade"
                            helperText={<ErrorMessage name='name' />}
                            error={props.errors.name && props.touched.name} />

                        </Grid>
                        <Grid item xs={12} sm={6} className="AppInput">
                          <Grid item xs={12} sm={6} className="AppInput">
                            <Autocomplete name="regiaoPai"
                              onChange={(e, v) => {
                                setFieldValue('regiaoPai', v);
                                // buscarPaises(v)
                              }}
                              fullWidth
                              getOptionLabel={option => option.name}
                              options={provincias} style={{ minWidth: 200 }}
                              renderInput={(params) => <TextField variant="outlined" margin="dense" label="Províncias"
                                fullWidth {...params} placeholder="Seleccione uma opção" />}
                            />

                          </Grid>

                        </Grid>

                      </Grid>
                      <DialogActions>
                        <Button onClick={handleClose}>Cancelar</Button>
                        <Button type="submit" onClick={handleClose}>Salvar</Button>
                      </DialogActions>
                    </Form>)
                }}
              </Formik>
            </DialogContent>


          </Dialog>
        </div>
      )

      }






    </div>

  );
}
